import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import OrderPageWrapper from "../components/OrderPageWrapper";
import bonus from '../../../assets/page/Order/bonus.svg';
import {bonusInformation} from './list';

const OrderBonus = (props) => {

    const [ bonusAmount, setBonusAmount ] = useState(0);
    const [percentUserBonus, setPercentUserBonus] = useState(5);
    const [ totalSum, setTotalSum ] = useState(0);
    const [ toNextLevel, setToNextLevel ] = useState(5000);

    useEffect(() => {
                const bonusAmount = props.user.sumUserBonus;
                const totalOrdersSum = props.user.sumUserOrders;
                const percentUserBonus = props.user.percentUserBonus;
                let toNextLevel;
                if ( totalOrdersSum < 5000 ) {
                    toNextLevel = 5000 - totalOrdersSum;
                } else if ( totalOrdersSum >= 5000 & totalOrdersSum < 10000 ) {
                    toNextLevel = 10000 - totalOrdersSum;
                } else {
                    toNextLevel = 0;
                }
                setPercentUserBonus(percentUserBonus ? percentUserBonus : 5);
                setBonusAmount(bonusAmount);
                setTotalSum(totalOrdersSum);
                setToNextLevel(toNextLevel);
    }, [props.user]);

    const {t} = useTranslation()

    return (
            <OrderPageWrapper 
            {...props}
            title={t('order.bonusAccount.title')}
            bonusAmount={bonusAmount}>
            <div className={'order_bonus'}>
                <div className="order_bonus_icons">
                    <div className={'img'}>
                        <img
                            src={bonus}
                            alt=""
                        />
                    </div>
                    <div className={'percents'}>
                        <div className={'list'}>
                            <li className={percentUserBonus === 5 ? 'active' : undefined}>
                                5%
                            </li>
                            <li className={percentUserBonus === 7 ? 'active center' : 'center'}>
                                7%
                            </li>
                            <li className={percentUserBonus === 10 ? 'active' : undefined}>
                                10%
                            </li>
                        </div>
                    </div>
                </div>
                <div className={'order_bonus_information'}>
                    {toNextLevel !== 0 ? 
                    <p>
                        {t('order.bonusAccount.youOrdered')} <b>{totalSum} .</b>  {t('order.bonusAccount.left')} <b>{toNextLevel} .</b> {t('order.bonusAccount.levelOfBonuses')}
                    </p> :
                    <p>
                        {t('order.bonusAccount.youOrdered')} <b>{totalSum} .</b>      !
                    </p>}
                    <h1>
                        {t('order.bonusAccount.bonusProgram')}
                    </h1>
                    {
                        bonusInformation.map(el =>
                            <li key={el.id}>
                                {t(`${el.text}`)}
                            </li>
                        )
                    }
                </div>
            </div>
        </OrderPageWrapper>
    );
}

export default OrderBonus;