import React, { useEffect, useState } from 'react';
import axios from 'axios';
import store from 'store';
import { backendUrl } from '../../config/config';
import {Switch, Route, Redirect} from 'react-router-dom';
import { useCustomStore } from '../../context/useStore';
import { ACTIONS } from '../../context/providerContext';

import './index.scss';
import {routes} from './routes';
import ErrorIndicator from '../../components/Errors/ErrorIndicator/index';
import Spinner from '../../components/Spinner/index';
import OrderHistory from "./OrderHistory";
import OrderBonus from "./OrderBonus";
import OrderPersonal from "./OrderPersonal";
import ChangePassword from "./ChangePassword";

const Order = (props) => {

    const path = props.match.path.slice(0, 3);

    const [ loading, setLoading ] = useState(false);
    const [ user, setUser ] = useState(null);
    const [ orders, setOrders ] = useState(null);
    const [ error, setError ] = useState(false);
    const { dispatch } = useCustomStore();

    useEffect(() => {

        setLoading(true);

        axios.get(backendUrl + '/user/', {
            headers: {
                'Authorization': `Bearer ${store.get('24rolls').user.token}`
            }
        })
        .then(res => {
            console.log(res);
            setUser(res.data.user);
            setOrders(res.data.orders);
            setError(false);
            setLoading(false);
            dispatch({ type: ACTIONS.SET_BONUS_AMOUNT, bonus: res.data.user.sumUserBonus });
        })
        .catch(err => {
            console.log(err, err.response);
            setError(true);
            setLoading(false);
        })
    }, []);

    useEffect(() => {
        if (error) {
            window.location.reload();
        }
    }, []);

    return (
    <div style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center'
        }}
    >
        { loading && !error && <Spinner /> }
        { error && !loading && <ErrorIndicator /> }
        { user && orders &&  
            <Switch>
                <Route exact path={path + "/order"} render={() => (<Redirect to={path + routes.personal} />)} />
                <Route 
                    exact 
                    path={path + routes.bonus} 
                    render={(props) => <OrderBonus {...props} user={user} />} 
                />
                <Route 
                    exact 
                    path={path + routes.history} 
                    render={(props) => <OrderHistory {...props} user={user} orders={orders} />} 
                />
                <Route 
                    path={path + routes.personal} 
                    render={(props) => <OrderPersonal {...props} user={user} />} 
                />
                <Route 
                    path={path + routes.changePassword} 
                    render={(props) => <ChangePassword {...props} user={user} />} 
                />
                <Route path={path + routes.logout} />
            </Switch>
        }
    </div>
    )
}

export default Order;