import { props } from 'ramda';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

import avatar from '../../../assets/page/Order/default-avatar.png';

const OrderHistoryFooter = ({footer}) => {

    const defaultOrders = footer.slice(0, 3);
    const [ orders, setOrders ] = useState(defaultOrders);

    const {t} = useTranslation();
    const [ showMoreOrders, setMoreOrders ] = useState(false);

    const onShowMoreOrdersButtonClick = () => {
        setMoreOrders(true);
        setOrders(footer);
    };

    const onCloseMoreOrders = () => {
        setMoreOrders(false);
        setOrders(defaultOrders);
        window.scrollTo(0, 0);
    };

    return (
        <div className={'order_history_footer'}>
            {
                orders.map((orders, index) =>
                    <div
                        key={index}
                        className={'order_history_footer_list'}
                    >
                        <div className={'content'}>
                            <div className={'content_header'}>
                                <div className={'content_header_left'}>
                                    <h1>{t('order.historyOrders.order')}</h1>
                                    <h1>{orders.id}</h1>
                                    <span
                                        style={{
                                            background: orders.status.color,
                                            borderRadius: '2px',
                                            color: orders.status.color === 'forestgreen' ? '#ededed': 'black',
                                            padding: '3px 18px',
                                            fontWeight: 600
                                        }}
                                    >
                                        {orders.status.name}
                                    </span>
                                </div>
                                <div className={'content_header_right'}>
                                    <img
                                        src={avatar}
                                        alt=""
                                    />
                                    <p>{orders.name}</p>
                                    <span>
                                        {
                                        orders.date.slice(-2) + 
                                        '.' + orders.date.slice(5, 7) + 
                                        '.' + orders.date.slice(0, 4)
                                        }
                                        </span>
                                    <span>{orders.time}</span>
                                </div>
                            </div>
                            <div className={'content_body'}>
                                {
                                    orders.order.map((el, id) => {
                                        if ( el.name !== " " ) return (
                                            <li
                                                key={id}
                                                className={'content_body_block'}
                                            >
                                                <div className={'left'}>
                                                    <p>{el.name}</p>
                                                </div>
                                                <div className={'right'}>
                                                    <span>{el.count}x{el.price} .</span>
                                                    <span>{el.count * el.price} .</span>
                                                </div>
                                            </li>
                                        );
                                    })
                                }
                            </div>
                            <div className={'content_footer'}>
                                <h2>{t('order.historyOrders.orderPrice')}</h2>
                                <h1>{orders.price} .</h1>
                            </div>
                        </div>
                    </div>
                )
            }
            {orders.length > 3 && 
            <button 
            onClick={showMoreOrders ? onCloseMoreOrders : onShowMoreOrdersButtonClick}>
                { showMoreOrders ? 'c' : '...' }
            </button>
            }
        </div>
    )
}

export default OrderHistoryFooter;