import React from 'react';
import { useTranslation } from 'react-i18next';

import OrderPageWrapper from "../components/OrderPageWrapper";

const allStatuses = {
    'check': {
       name: ' ',
       color: 'forestgreen'
    },
    'no-call': {
        name: '',
        color: '#dc143c'
    },
    'new': {
        name: '',
        color: 'forestgreen'
    },
    'send-to-delivery': {
        name: ' ',
        color: '#ffc84c'
    },
    'complete': {
        name: '',
        color: 'forestgreen'
    },
    'send-to-assembling': {
        name: '',
        color: '#fff44c'
    },
    'delivering': {
        name: '',
        color: '#ffc84c'
    },
    'client-confirmed': {
        name: '',
        color: '#fff44c'
    },
    'samovivoz': {
        name: '',
        color: 'forestgreen'
    },
    'cancel-other': {
        name: '',
        color: '#dc143c'
    },
    'prepayed': {
        name: '',
        color: '#fff44c'
    },
    'assembling': {
        name: ' ',
        color: '#ffc84c'
    }
}

const OrderHistory = (props) => {
    
    const newOrders = props.orders.map(el => {
            let status = allStatuses[el.status];
            return {
                id: el.number,
                 name: props.user.firstName,
                 date: el.markDatetime.slice(0, 10),
                 time: el.markDatetime.slice(11, 16),
                 status: status,
                 price: el.totalSumm,
                 order: el.items.map((product, index) => ({
                     id: index,
                     name: product.offer.displayName,
                     price: product.initialPrice,
                     count: product.quantity
                 }))
            }
         });

    const {t} = useTranslation()

    return (
         <OrderPageWrapper
            {...props}
            title={t('order.historyOrders.title')}
            bonusAmount={props.user.sumUserBonus}
            orderFooter={newOrders}
         >
            {
            !props.orders && 
            <div className={'order_history'}>
                <p>{t('order.historyOrders.notAnyOrders')}</p>
            </div>
            }
        </OrderPageWrapper>
    );
}

export default OrderHistory;